
import AdminLayout from './AdminLayout';
import AdminStyleSheet from './adminStyleSheet';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSearch, faBars, faPersonBooth, faUsers, faListAlt, faWarehouse, faTags, faPercent, faChartBar } from '@fortawesome/free-solid-svg-icons';
import { faProductHunt } from '@fortawesome/free-brands-svg-icons';

import { Nav, Form, FormControl, Button, Spinner, Tab, Row, Col, } from "react-bootstrap"

import Vendors from './admin-dashboard/vendors';
import Customers from './admin-dashboard/customers';
import Products from './admin-dashboard/products';
import ProductCategories from './admin-dashboard/product-categories';
import Inventory from './admin-dashboard/inventory';
import Discounts from './admin-dashboard/discount';
import Commision from './admin-dashboard/commision';
import Reports from './admin-dashboard/reports';

const Dashboard = props => (
    <div>
            <Tab.Container id="dashboard-tabs" defaultActiveKey="one">
                {/* Toogle Button Components */}
                <div className="side_tab_toogle_btn" style={{ width: '100%' }}>
                    <Form inline>
                        <Button style={AdminStyleSheet.side_drawer_search_bars} onClick={props.drawerClickHandler}>
                            <FontAwesomeIcon icon={faBars} />
                        </Button>
                        <FormControl type="text" placeholder="Search" className="mr-sm-6" style={{ flex: '1', margin: '0px 5px' }}></FormControl>
                        <Button style={AdminStyleSheet.side_drawer_search_bars}>
                            <FontAwesomeIcon icon={faSearch} />
                        </Button>
                    </Form>
                </div>
                {/* End of the Toogle Button Components */}


                <Row style={{ padding: '0px', margin: '0px' }}>

                    {/* Tabs Components */}
                    <div className="tabs">
                        <Col sm={2} style={{ padding: '0px', maxWidth: '280px', minWidth: '110px' }}>
                            <Nav className="flex-column" variant='tabs'>
                                <Form inline>
                                    <FormControl style={AdminStyleSheet.search_bar} type="text" placeholder="Search" className="mr-sm-6"></FormControl>
                                    <Button style={AdminStyleSheet.search_btn}>
                                        <FontAwesomeIcon style={AdminStyleSheet.tabs_fontawesome} icon={faSearch} />
                                    </Button>
                                </Form>
                                <Nav.Item>
                                    <div className="hover" style={AdminStyleSheet.border}>
                                        <Nav.Link eventKey="one" style={AdminStyleSheet.link_color}>
                                            <FontAwesomeIcon icon={faPersonBooth} style={{ marginRight: '20px' }} />
                                            Vendors
                                                </Nav.Link>
                                    </div>
                                </Nav.Item>
                                <Nav.Item>
                                    <div className="hover" style={AdminStyleSheet.border}>
                                        <Nav.Link eventKey="two" style={AdminStyleSheet.link_color}>
                                            <FontAwesomeIcon icon={faUsers} style={{ marginRight: '20px' }} />
                                            Customers
                                                 </Nav.Link>
                                    </div>
                                </Nav.Item>
                                <Nav.Item>
                                    <div className="hover" style={AdminStyleSheet.border}>
                                        <Nav.Link eventKey="three" style={AdminStyleSheet.link_color}>
                                            <FontAwesomeIcon icon={faProductHunt} style={{ marginRight: '20px' }} />
                                            Products
                                                </Nav.Link>
                                    </div>
                                </Nav.Item>
                                <Nav.Item>
                                    <div className="hover" style={AdminStyleSheet.border}>
                                        <Nav.Link eventKey="four" style={AdminStyleSheet.link_color}>
                                            <FontAwesomeIcon icon={faListAlt} style={{ marginRight: '20px' }} />
                                            Categories
                                                </Nav.Link>
                                    </div>
                                </Nav.Item>
                                <Nav.Item>
                                    <div className="hover" style={AdminStyleSheet.border}>
                                        <Nav.Link eventKey="five" style={AdminStyleSheet.link_color}>
                                            <FontAwesomeIcon icon={faWarehouse} style={{ marginRight: '20px' }} />
                                            Inventory
                                                </Nav.Link>
                                    </div>
                                </Nav.Item>
                                <Nav.Item>
                                    <div className="hover" style={AdminStyleSheet.border}>
                                        <Nav.Link eventKey="six" style={AdminStyleSheet.link_color}>
                                            <FontAwesomeIcon icon={faTags} style={{ marginRight: '20px' }} />
                                            Discounts
                                                </Nav.Link>
                                    </div>
                                </Nav.Item>
                                <Nav.Item>
                                    <div className="hover" style={AdminStyleSheet.border}>
                                        <Nav.Link eventKey="seven" style={AdminStyleSheet.link_color}>
                                            <FontAwesomeIcon icon={faPercent} style={{ marginRight: '20px' }} />
                                            Commision
                                                </Nav.Link>
                                    </div>
                                </Nav.Item>
                                <Nav.Item>
                                    <div className="hover" style={AdminStyleSheet.border}>
                                        <Nav.Link eventKey="eight" style={AdminStyleSheet.link_color}>
                                            <FontAwesomeIcon icon={faChartBar} style={{ marginRight: '20px' }} />
                                            Reports
                                                </Nav.Link>
                                    </div>
                                </Nav.Item>
                            </Nav>
                        </Col>
                    </div>
                    <div className="tab_content">
                        <Col sm={"auto"} style={{ padding: '5px', margin: '0px', minWidth: '100%' }}>
                            <Tab.Content>
                                <Tab.Pane eventKey="one">
                                    <Vendors />
                                </Tab.Pane>
                                <Tab.Pane eventKey="two">
                                    <Customers />
                                </Tab.Pane>
                                <Tab.Pane eventKey="three">
                                    <Products />
                                </Tab.Pane>
                                <Tab.Pane eventKey="four">
                                    <ProductCategories />
                                </Tab.Pane>
                                <Tab.Pane eventKey="five">
                                    <Inventory />
                                </Tab.Pane>
                                <Tab.Pane eventKey="six">
                                    <Discounts />
                                </Tab.Pane>
                                <Tab.Pane eventKey="seven">
                                    <Commision />
                                </Tab.Pane>
                                <Tab.Pane eventKey="eight">
                                    <Reports />
                                </Tab.Pane>
                            </Tab.Content>
                        </Col>
                    </div>
                    {/* End of the Tabs Components */}
                </Row>
            </Tab.Container>
        <style jsx>
            {`
                .hover:hover {
                    margin-left: 10px;
                    margin-right: -10px;
                    margin-top: -4px;
                    margin-bottom: 4px;
                }
                @media (max-width: 991px) {
                    .tabs {
                      display: none;
                    }
                    .tab_content {
                        display: none;
                    }
                }
                @media (min-width: 992px) {
                    .side_tab_toogle_btn {
                        display: none;
                    }
                }
            `}
        </style>
    </div>
)

export default Dashboard;